
/* ==================== common styles ======================= */
.qqqr {
  outline: 1px solid red;
}

.qqqc {
  outline: 1px solid cyan;
}


html {
  font-size: 18px;
  /*used for rem-units*/
}

body {
  min-width: 320px;
  overflow-x: hidden;
  background-color: #fff;
  color: $colorTextRegular;
  font-family: $fontRegular;
  font-size: 18px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0, .5);
}

.flex {
  display: flex;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  outline: none;
  border: none;
}

p {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 25px;
}

input,
input:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #888;
}

::-moz-placeholder {
  color: #888;
}

:-ms-input-placeholder {
  color: #888;
}

::placeholder {
  color: #888;
}

section {
  position: relative;
  padding: 80px 0;
  overflow: hidden;
}

.wrapper {
  outline: 1px solid blue;
  position: relative;
  width: 98%;
  max-width: 1110px;
  margin: 0 auto;
}


// .header-social__link--ig {
//   @include sprite($s-ig);
// }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $fontEmphasis;
  color: $colorTextHeader;
}

/*------------------------------ Header ------------------------------*/
.header {
  position: relative;
  background: #555;
  overflow: hidden;
  color: $colorLightest;
  max-height: 910px;
  overflow: hidden;
}


.slide-item {
    img {
    display: block;
    width: 100vw;
    margin: 0 auto;
  }
}

.img-texture {
  position: relative;
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/mainSlider/mask.jpg);
    opacity: .85;
  }
}

.slider-controls {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
   .wrapper {
    width: 100%;
    justify-content: space-between;
   }
}

.prev,
.next {
  display: block;
  cursor: pointer;
  opacity: 0.8;
  transition: all .4s;
  &:hover {
    opacity: 1;
  }
}

.prev {
  @include sprite($s-leftangle);
}

.next{
  @include sprite($s-rightangle);
}

.header-content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  align-content: space-between;
  flex-wrap: wrap;
  padding: 1% 0 5%;
}
    
.header-top__contacts,
.header-nav {
  width: 100%;
  flex-basis: 100%;
}

.header-social {
  li,
  a {
    display: inline-block;
  }
  li {
    margin-right: 4px;
  }
  a {
    opacity: 0.8;
    transition: all .4s;
    &:hover {
      opacity: 1;
    }
  }
}

.header-social__link--tw {
  @include sprite($s-tw);
}

.header-social__link--ig {
  @include sprite($s-ig);
}

.header-social__link--fb {
  @include sprite($s-fb);
}

.header-top__contacts {
  justify-content: space-between;
}

.header-contacts {
  align-items: center;
  font-size: 16px;
  li,
  a {
    display: inline-block;
    white-space: nowrap;
  }
  li {
    margin-left: 30px;
  }
  a:before {
    content: "";
    display: inline-block;
    margin-right: 21px;
    vertical-align: middle;
  }
}

.decorated {
  position: relative;
  display: inline-block;
  padding-bottom: 5px;
  &:after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 1px;
    width: 0%;
    background: $colorLightest;
    transition: all .4s;
  }
  &:hover,
  &:active {
    &:after {
      width: 100%;
    }
  }
}

.header-contacts__link--map {
  &:before {
    @include sprite($s-mappin);
  }
}

.header-contacts__link--phone {
  &:before {
    @include sprite($s-phone);
  }
}
.header-contacts__link--mail {
  &:before {
    @include sprite($s-mail);
  }
}

/*main navigation*/
.header-nav {
  justify-content: space-between;
}

.header-logo {
  width: 20%;
  .logo {
    width: 100%;
  }
}

.header-nav__links {
  justify-content: space-around;
  align-items: flex-end;
  width: 40%;
  li {
    padding: 0 1%;
  }
  a {
    position: relative;
    font-size: 18px;
    display: block;
    font-family: $fontEmphasis;
    text-transform: uppercase;
    padding: 12px 0;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0;
      height: 4px;
      transition: all .4s;
      background: $colorBright;
    }
    &:hover,
    &:active,
    &.active {
      &:before {
        width: 100%;
      }
    }
  }
}

/*------------------------------ services-list ------------------------------*/

.services-list {
  flex-wrap: wrap;
  justify-content: space-between;
  color: $colorLightest;
  h3 {
    color: $colorLightest;
  }
}

.service-item {
  position: relative;
  max-width: 350px;
  flex-basis: 32%;
  flex-shrink: 0;
  height: 265px;
  margin-bottom: 36px;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  &:after {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: $colorTextRegular;
    opacity: .7;
    transition: all .4s;
    z-index: 1;
  }
  >h3 {
    position: absolute;
    bottom: 40px;
    left: 28px;
    font-size: 24px;
    transition: all .4s;
    z-index: 2;
  }
  .description {
    position: absolute;
    opacity: 0.9;
    background: $colorBright;
    transition: all .4s;
    top: 101%;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
  }
  &:hover {
    &:after {
      top: 100%;
      bottom: -100%;
    }
    >h3 {
    opacity: 0;
    }
    .description {
      top: 0;
    }
  }
}

.description {
  padding: 54px 25px;
  h3 {
    font-size: 24px;
    margin-bottom: 17px;
  }
  p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 40px;
    max-width: 230px;
  }
  a {
    font-size: 16px;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }

}

.powder-painting {
  background-image: url(../img/servicesPreviews/service-1.jpg);
}

.immersion-print {
  background-image: url(../img/servicesPreviews/service-2.jpg);
}

.chemical-metallization {
  background-image: url(../img/servicesPreviews/service-3.jpg);
}

.sandblasting {
  background-image: url(../img/servicesPreviews/service-4.jpg);
}

.shot-blasting {
  background-image: url(../img/servicesPreviews/service-5.jpg);
}

.spraygun-painting {
  background-image: url(../img/servicesPreviews/service-6.jpg);
}

.candy-painting {
  background-image: url(../img/servicesPreviews/service-7.jpg);
}

.aerography {
  background-image: url(../img/servicesPreviews/service-8.jpg);
}

.tire-fitting {
  background-image: url(../img/servicesPreviews/service-9.jpg);
}

/*------------------------------ banner section ------------------------------*/
.banner-section {
  padding: 0;

  background-image: url(../img/bannerBg.jpg);
  background-repeat: repeat;
 
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: $colorBright;
    opacity: .85; 
  }
  .wrapper  {
    position: relative;
    z-index: 2;
    height: 80vh;
    min-height: 370px;
    max-height: 650px;
    align-content: center;
    align-items: center;

    // &:after {
    //   content: "";
    //   display: block;
    //   position: absolute;

    //   bottom: 0;

    //   right: 0;
    //   width: 100%;
    //   height: 100%;
    //   background-image: url(../img/wheel.png);
    //   background-repeat: no-repeat;
    //   background-position: 80% -36%;
    //   //background-size: 60%;
    //   z-index: 2;
    // }
  }
}

.banner-content {
  max-width: 540px;
  flex-wrap: wrap;
  position: relative;
  z-index: 3;
  color: $colorLightest;
  h2 {
    width: 100%;
    color: $colorLightest;
    font-size: 60px;
    line-height: 1.2;
    margin-bottom: 20px;
  }
  p {
    font-size: 18px;
    margin-bottom: 65px;
  }
}

.button {
  display: block;
  width: 220px;
  height: 55px;
  line-height: 55px;
  font-family: $fontEmphasis;
  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
}

.banner-button {
  position: relative;
  top: 0;
  color: $colorTextHeader;
  background: $colorLightest;
  transition: all .4s;
  &:hover {
    box-shadow: 0 30px 60px rgba( 0,0,0, .3);
    top: -1px;
  }
}

.banner-img {
  position: absolute;
  bottom: -30%;
  right: -20%;
  height: 130%;
}

/*------------------------------ news section ------------------------------*/
.news-section {
  position: relative;
  padding-bottom: 0;
  overflow: hidden;
  &:after {

    outline: 1px solid lime;
    content: "";
    display: block;
    position: absolute;
    bottom: -15%;
    right: 0;
    width: 100vh;
    height: 100vh;
    background-image: url(../img/newspapers.png);
    background-repeat: no-repeat;
    background-position: 100% 100%;
    background-size: contain;
  }
  

}

.about-text {
  margin-right: 30px;
  margin-bottom: 5%;
  padding-top: 5px;
  h2 {
    font-size: 48px;
    margin-bottom: 18px;
  }
  h4 {
    font-size: 20px;
    max-width: 470px;
    line-height: 1.4;
    margin-bottom: 18px;
  }
  p {
    line-height: 1.5;
    margin-bottom: 27px;
    &:last-of-type {
      margin-bottom: 43px;
    }
  }
}

.about-button {
  color: $colorLightest;
  background: $colorBright;
}

.about-img {
  width: 100%;
  max-width: 540px;
}

.news-block {
  h2 {
    font-size: 48px;
    margin-bottom: 50px;
    text-align: center;
  }
}

.news-list {
  max-width: 735px;
}

.news-item {
  margin-bottom: 24px;
}

.news-content {
  padding: 12px 1% 18px; 
  h3 {
    font-size: 24px;
    margin-bottom: 18px;
  }
  p {
    line-height: 1.5;
    margin-bottom: 28px;
  }
}

.read-more {
  font-family: $fontEmphasis;
  font-size: 18px;
  &:after {
    content: "";
    display: inline-block;
    @include sprite($s-readMoreArrow);
    margin-left: 10px;
  }
}

/*------------------------------ map section ------------------------------*/
.map-section {
  padding: 0;
}

.map {
  height: 100vh;
  min-height: 240px;
  max-height: 480px;
  background: #888;
  filter: grayscale(100%);
}

/*------------------------------ footer  ------------------------------*/

.footer {
  background: $colorBright;
  color: $colorLightest;
  font-size: 16px;
  padding: 57px 0 53px;
}

/*footer top*/
.footer-top {
  justify-content: space-between;
}

.footer-logo,
.footer-nav,
.footer-contacts {
  outline: 1px solid lime;
  width: 30%;
}

.footer-logo {
  .logo {
    margin-bottom: 28px;
    width: 160px;
    height: 160px;
  }
  p {

  }
}

.footer-nav {
  justify-content: space-between;
}

.footer-nav__links {
  flex-basis: 40%;

  outline: 1px solid cyan;

  li {
    margin-bottom: 10px;
  }
  a {
    display: inline-block;
    position: relative;
    font-family: $fontEmphasis;
    font-size: 18px;
    text-transform: uppercase;
    padding-bottom: 8px;
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 0%;
      height: 2px;
      background: $colorLightest;
      transition: all .4s;
    }
    &:hover,
    &:active,
    &.active {
      &:after {
        width: 100%;
      }
    }
  }
}

.footer-contacts {
  li {
    margin-bottom: 13px;
  }
  a {
    display: inline-block;
    position: relative;
    &:before {
      content: "";
      display: inline-block;
      margin-right: 20px;
      vertical-align: middle;
    }
  }
}

.footer-contacts__link--map:before {
  @include sprite($s-mappinLight);
}

.footer-contacts__link--phone:before {
  @include sprite($s-phoneLight);
}

.footer-contacts__link--mail:before {
  @include sprite($s-mailLight);
}
/*footer bottom*/
.footer-bottom {
  padding: 10px 0;
  justify-content: space-between;
  align-items: center;
  align-content: space-around;
}

.footer-social {
  li {
    display: inline-block;
  }
  a {
    opacity: .9;
    transition: all .4s;
    &:hover {
      opacity: 1;
    }
  }
}

.footer-social__link--tw {
  display: block;
  @include sprite($s-tw);
}

.footer-social__link--ig {
  display: block;
  @include sprite($s-ig);
}

.footer-social__link--fb {
  display: block;
  @include sprite($s-fb);
}

.copy {
  margin-bottom: 0;
  span {
    font-size: 20px;
  }
}